<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Notes') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <template v-if="IsNoteList">
                            <div class="col-md-12 p-0">
                                <div class="heading-border mb-3">
                                    <h5 class="h5">
                                        <span> {{ $t('Notes') }}</span>
                                        <small class="float-right">
                                            <a class="addnotes" title="Add Note" @click="AddNote"><i
                                                    class="fa fa-plus pr-2"></i>{{
                                                        $t('AddNote') }}</a>
                                        </small>
                                    </h5>
                                </div>
                            </div>
                        </template>
                        <template v-if="IsNoteList">
                            <span class="legend-Progress colorboxsquare"></span>{{ $t('Private') }}
                            <span class="legend-upcoming colorboxsquare ml-3"></span>{{ $t('Public') }}
                            <div class="w-100 pt-2" v-if="NoteList.length > 0">
                                <TicketNotes :NoteData="NoteList"></TicketNotes>
                            </div>
                        </template>
                        <template v-if="!IsNoteList">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="UploadFileWithNote">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="Descriptionn"></ckeditor>
                                </template>
                            </dynamic-form>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from '../../services/DataService'
import TicketNotes from '../../components/Modal/TicketNotes.vue';
// import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import CKEditor from '@ckeditor/ckeditor5-vue2';

export default {
    components: { 
        TicketNotes,
        "ckeditor": CKEditor.component
    },
    props: {
        WorkOrderDetails: {
            Type: Object,
            required: true
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            isLoading: false,
            IsNoteList: true,
            NoteList: [],
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Note Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: "",
                            name: "QuickViewType",
                            value: 2,
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                            config: {
                                options: [{ name: 'Private', value: 1 }, { name: 'Public', value: 2 }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },

                        }
                    ],
                },
                {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "NoteAttachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.ShowNoteList
                }
            ],
            ticketModel:{
                NoteDescription:'',                           
                transferType:'',
                TicketId: 0,
                ModuleName: '',
                SubModuleCode:'',
                NoteId:0,
                WorkOrderId:0,
                fileData:[]
            },
            ticketType:'',
            Descriptionn:null,
        }
    },
    created: function () {
        this.LoadWorkorderNotes(this.WorkOrderDetails.ticket_id);
    },
    methods: {
        Close() {
            this.$parent.shownotelist = false;
        },
        AddNote() {
            this.IsNoteList = false;
        },
        ShowNoteList(){
            this.IsNoteList = true;
        },
        async LoadWorkorderNotes(ths) {
            debugger
            var vm = this;
            vm.NoteList = [];
            vm.isLoading = true;
            var url = `TicketId=${ths}`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                debugger
                vm.isLoading = false;
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                }
                else {
                    vm.noRecord = true;
                }
            });
        },
        onChangeRadio: function(e,field){          
            this.ticketType=e.target.getAttribute('data-value');
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];  
                var f = evt.target.files[0];
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        
                        vm.fileExt  += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,    
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);                       
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch(ex){                
            }
        }, 
        UploadFileWithNote: function (formfields, val) {            
            var vm = this;
            vm.ticketModel.transferType = vm.ticketType==1?'Private':'Public';;
            vm.ticketModel.NoteDescription = vm._data.Descriptionn;
            vm.ticketModel.TicketId = vm.WorkOrderDetails.ticket_id;
            vm.ticketModel.ModuleName = 'FSM';
            vm.ticketModel.SubModuleCode = 'FSM_WORKORDER';
            vm.ticketModel.fileData = vm.fileData;
            var formData = JSON.stringify(vm.ticketModel);
            DataServices.AddNote(formData).then((response) => {
               try {                   
                    if (response.data != null && response.data != "" && response.data.status == "Success") {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Notes")]), "success", true, vm.$t("Alert"));                        
                        vm.LoadWorkorderNotes(this.WorkOrderDetails.ticket_id);
                        vm.ShowNoteList();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("Notes")]), "failure", true, vm.$t("Alert"));                     
                    }
                } catch (exinner) {
                    return HandleErrorOccured(exinner, 'saveNote');
                }
            });
        }
    }
}
</script>