<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('WorkOrderStatus') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        WorkOrderDetails: {
            Type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: { "required": true },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ]
        }
    },
    created: function () {
        var vm = this;
        vm.GetDropDownData('WORK_ORDER_FINAL_STATUS');
        vm.FormSchema[0].Data[0].value = vm.WorkOrderDetails.WO_Status_Id;
    },
    methods: {
        Close() {
            this.$parent.statuslist = false;
        },
        GetDropDownData: function (type) {
            var vm = this;
            vm.isLoading = true;
            var url = `id=&moduleName=FSM&type=${type}&search=&refId=`;
            DataService.GetDDLData(url).then(response => {
                if (response.status == 200) {
                    if (type == "WORK_ORDER_FINAL_STATUS") {
                        response.data.DATA.forEach((item) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
                vm.isLoading = false;
            })
        },
        OnSubmit(formfields, val) {            
            var vm=this;
            vm.isLoading=true;
            var jsonobj = {
                Id: vm.WorkOrderDetails.id,
                StatusId: formfields.Status,
            };
            var params = JSON.stringify(jsonobj);
            DataService.MarkAsDone(params).then((response) => {                
                if (response.data == 1) {                   
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));      
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileUpdating", [vm.$t("Status")]), "failure", true, vm.$t("Alert"));                         
                }
                vm.isLoading=false;
            });
        }
    }

}
</script>