<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="theme-primary partition-full">
            <div class="p-actions legends_topbar float-right">
                <div class="p-action-map collapse_map">
                    <a href="javascript:;" class="e-c-map p-action-btn text-white align-items-center d-flex "
                        v-on:click="ShowHideMap"><em id="icnToggleMap" class="fa pr-2"
                            :class="{ 'fa-angle-double-up': isShowMap == true, 'fa-angle-double-down': isShowMap == false }"></em><span
                            id="spnToggleMap">{{ mapHeaderText }}</span></a>
                </div>
            </div>
        </div>
        <MapComponent v-if="isShowMap" class="row" mapContainer="workOrderMapDiv" mapType="Google" :Data="mapObj"
            :latLng="latLng" :config="config"></MapComponent>
        <tg-list ref="tgList" v-show="IsWorkOrderView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_WORKORDER"
            :LegendArray="[]" :widgets="widgets" IdentityColumn="id" :HeaderText="$t('WorkOrder')"
            :listType="['List', 'Card']" :ListData="WorkOrderData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="fetchRecords" v-on:ApplyView="OnApplyView" :ViewList="ViewList"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :RenderRowActionMethod="renderActions"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'workorder_number'">
                    <RouterLink :title="data.row[data.column.COLUMN_NAME]"
                        :to="'/Ticketing/WorkOrderDetail/' + data.row.id + '/' + data.row.ticket_id"
                        data-toggle-tooltip="tooltip">
                        {{ data.row[data.column.COLUMN_NAME] }}</RouterLink>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'sa_status'">
                    <a>
                        <span>
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>                        
                    </a>
                </template>
            </template>
            <template v-slot:customcard="{ data }">
                <div class="card-box expense-card px-2 d-flex flex-column justify-content-between blue-bg-light">
                    <div class="d-block mb-3 float-left">
                        <div class="expense-card-header">
                            <div class="card-number">
                                #{{ data.row.workorder_number }} ({{  (data.row.status_id) }})
                            </div>
                        </div>
                        <div class="card-detail" v-if="(data.row.subject != '' && data.row.subject != null)"><i
                                class="fa fa-file-text" aria-hidden="true"></i><span>{{ data.row.subject
                                }}</span></div>
                        <div class="card-detail" v-if="(data.row.worktype_name != '' && data.row.worktype_name != null)"><i
                                class="fa fa-info-circle" aria-hidden="true"></i><span data-toggle-tooltip="tooltip"
                                data-original-title="Work Type" style="width:auto">{{
                                    data.row.worktype_name }}</span></div>
                        <div class="card-detail" v-if="(data.row.item_name != '' && data.row.item_name != null)"><i
                                class="fa fa-file" aria-hidden="true"></i>
                            <span>{{ data.row.item_name }}</span>
                        </div>
                        <div class="card-detail" v-if="(data.row.assigned_to != '' && data.row.assigned_to != null)"><i
                                class="fa fa-user" aria-hidden="true"></i><span>{{ data.row.assigned_to
                                }}</span></div>
                        <div class="card-detail" v-if="data.row.Address != ''"><i class="fa fa-map-marker"
                                aria-hidden="true"></i>
                            <span>{{ data.row.Address }}</span>
                        </div>
                        <br />
                    </div>
                    <div class="scheduled_type">
                        <span class="text-info" style="font-weight: 500;"
                            v-if="data.row.SA_Scheduled_StartTime == null && data.row.SA_Scheduled_EndTime == null && data.row.StartDateTime != null">
                            Preferred Time </span>
                        <span class="text-success" style="font-weight: 500;"
                            v-if="data.row.SA_Scheduled_StartTime != null && data.row.SA_Scheduled_EndTime != null">
                            Scheduled Time </span>
                    </div>
                    <div class="bottom-action">
                        <div class="card-date">
                            <span class="text-info"
                                v-if="data.row.SA_Scheduled_StartTime == null && data.row.SA_Scheduled_EndTime == null && data.row.StartDateTime != null">
                                <span class="preferedtime">
                                    {{ $options.filters.formatDateTimee(data.row.StartDateTime) }}</span>
                                <span style="float: left;font-weight: bold !important;">-</span>
                                <span class="preferedtime">
                                    {{ $options.filters.formatDateTimee(data.row.EndDateTime) }}</span>
                            </span>
                            <span class="text-success"
                                v-if="data.row.SA_Scheduled_StartTime != null && data.row.SA_Scheduled_EndTime != null">
                                <span class="preferedtime">
                                    {{ $options.filters.formatDateTimee(data.row.SA_Scheduled_StartTime) }}</span>
                                <span style="float: left;font-weight: bold !important;">-</span>
                                <span class="preferedtime">
                                    {{ $options.filters.formatDateTimee(data.row.SA_Scheduled_EndTime) }}</span>
                            </span>
                        </div>
                        <div class="card-action">
                            <a href="javascript:;" class="round-icon-small btn-secondary" data-toggle-tooltip="tooltip"
                                data-original-title="ViewOnMap" @click="ViewonMap(data.row.ticket_id, data.row)">
                                <em class="fa fa-map-marker" aria-hidden="true"></em>
                            </a>
                            <a class="round-icon-small btn-warning" href="javascript:;" data-toggle-tooltip="tooltip"
                                data-original-title="Notes" @click="openNoteListPopUp(data.row)">
                                <em class="fa fa-sticky-note-o"></em>
                            </a>
                            <a href="javascript:;" class="round-icon-small btn-primary" data-toggle-tooltip="tooltip"
                                data-original-title="QuickView" @click="openQuickViewpopup(data.row)">
                                <em class="fa fa-eye" aria-hidden="true"></em>
                            </a>
                            <a id="WOStatus" v-if="isViewMode == 0" title="Status" class="round-icon-small btn-success"
                                @click="WorkOrderStatus(data.row)">
                                <em class="fa fa-tag"></em><span>Status</span>
                            </a>

                            <a data-toggle-tooltip="tooltip" data-original-title="Detail"
                                class="round-icon-small btn-primary-light" @click="WorkOrderDetails(data.row)">
                                <em class="fa fa-ticket" aria-hidden="true"></em>
                            </a>
                            <!-- <template v-if="isViewMode==0">
                                <a class="actions-onclick" id="markDone" v-on:click="MarkAsDone(data.row.id)" data-toggle-tooltip="tooltip" data-original-title="MarkAsDone" v-if="(item.child_status_id != null && item.child_status_id != '' && item.status_id != 'Completed' && item.approval_pending_count == 0)" v-bind:value="item.id">
                                    <em class="fa fa-check text-success"></em>                                                       
                                    <span>{{ $t('MarkAsDone') }}</span>
                                </a>
                            </template>
                            <a class="actions-onclick" id="markActive" v-on:click="MarkAsActive(data.row.id)" data-toggle-tooltip="tooltip" data-original-title="MarkAsActive" v-if="data.row.status_id == 'Completed'  &&  CompanyUserType == 'CA'">
                                <em class="fa fa-check text-success"></em>
                            <span>{{$t('MarkAsActive')}}</span>
                            </a> -->
                            <a data-toggle-tooltip="tooltip" data-original-title="Schedule" v-if="data.row.status_id != 'Completed' && data.row.status_id != 'Failed' &&  data.row.status_id != 'Canceled' && data.row.SA_Id != null"
                                class="round-icon-small btn-success-light" @click="openScheduledAppointmentPopup(data.row)">
                                <em class="fa fa-clock-o action-icon" aria-hidden="true"></em>
                            </a>                            
                            <a data-toggle-tooltip="tooltip" data-original-title="AddServiceAppointment" v-if="data.row.status_id != 'Completed' && data.row.status_id != 'Failed' && data.row.status_id != 'Canceled' && data.row.SA_Id == null"
                                class="round-icon-small btn-success-light" @click="openAddServiceAppointment(data.row)">
                                <em class="fa fa-calendar-plus-o action-icon" aria-hidden="true"></em>
                            </a>
                        </div>
                    </div>
                </div>
            </template>
        </tg-list>
        <AddWorkOrderModal v-if="AddWorkOrderModal" :WorkOrdrId="ticketSourceId"></AddWorkOrderModal>
        <ManageView v-if="ShowManageView" submodulecode="FSM_WORKORDER" modulename="Ticketing" v-on:close="ShowManageView = false"
            v-bind:callbackfunction="fetchRecords" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </ManageView>
        <modalquickviewdetails v-if="showquickviewModal" submodulecode="FSM_WORKORDER" modulename="Ticketing"
            v-on:close="showquickviewModal = false" :WorkOrderDetails="IsWorkOrderDetails" >
        </modalquickviewdetails>
        <NoteListPopUp v-if="shownotelist" :WorkOrderDetails="IsWorkOrderDetails" ></NoteListPopUp>
        <StatuslistPopUp v-if="statuslist" :WorkOrderDetails="IsWorkOrderDetails"></StatuslistPopUp>
        <SchduleAppointment v-if="IsSchduleAppointment" :SchduleAppointment="SchduleAppointment"></SchduleAppointment>
        <AddServiceAppointmentPopUp v-if="showPopupAddServiceAppointment"
            :SchduleObj="ObjectToAddServiceAppointment" :callfrom="workorder">
        </AddServiceAppointmentPopUp>
        <SearchFilter v-if="showFilter" submodulecode="FSM_WORKORDER" modulename="Ticketing" v-on:close="showFilter = false"
            v-bind:callbackfunction="fetchRecords" v-on:FilterData="FilterData">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </SearchFilter>
    </section>
</template>

<script>
import Schema from "@/views/WorkOrder/WorkOrderSchema";
import AddWorkOrderModal from '../../components/Modal/AddWorkOrderModal.vue'
import DataServices from "@/services/DataService";
import ManageView from '../../components/Common/view-layout.vue'
import modalquickviewdetails from "../../views/WorkOrder/QuickView.vue";
import MapComponent from "../../components/Common/MapComponent.vue";
import NoteListPopUp from '../WorkOrder/NoteModel.vue'
import StatuslistPopUp from '../WorkOrder/WorkOrderStatus.vue'
import SchduleAppointment from "../ServiceAppointment/SchduleAppointment.vue";
import AddServiceAppointmentPopUp from "./AddServiceAppointmentPopUp.vue";
import SearchFilter from '../../components/Common/search-filter.vue';
export default {
    components: { SearchFilter, AddWorkOrderModal, ManageView, modalquickviewdetails, MapComponent, NoteListPopUp, StatuslistPopUp, SchduleAppointment, AddServiceAppointmentPopUp },
    data() {
        return {
            isLoading: false,
            showFilter: false,
            config: {
                isShowDrawButton: false,
                isShowEtaMap: true,
                isShowLocator: true
            },
            mapHeaderText: 'Expand Map',
            ShowManageView: false,
            listActions: Schema.listActions,
            WorkOrderData: [],
            ViewList: [],
            AddWorkOrderModal: false,
            workorder: 'WorkOrder',
            widgets: [
                {
                    icon: "fa fa-list-ul",
                    value: 0,
                    name: "Completed",
                    class: "one_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-ticket",
                    value: 0,
                    name: "In progress",
                    class: "two_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-line-chart",
                    value: 0,
                    name: "Need To Reschedule",
                    class: "three_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-reply",
                    value: 0,
                    name: "Scheduled",
                    class: "four_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-calendar-check-o",
                    value: 0,
                    name: "Unscheduled",
                    class: "five_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-file-text",
                    value: 0,
                    name: "Cancelled",
                    class: "six_wid",
                    onClick: this.widgetClick
                }
            ],
            isShowMap: false,
            noRecord: false,
            WorkOrderListing: true,
            viewType: 'List View',
            classView: 'fa fa-th-list',
            IsSchduleAppointment: false,
            WorkOrderCard: false,
            showWorkOrderModal: false,
            Headers: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            str_ticket_id: '',
            TicketID: '',
            IsSupervisor: 0,
            IsLoad: false,
            IsLocator: 0,
            UserType: '',
            Latitude: '',
            Longitude: '',
            MapIndex: null,
            CurrentItem: null,
            ShowLocatorMap: false,
            ShowSupervisorMap: false,
            TicketIdNumber: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            mapAPIKey: '',
            MapType: '',
            map: '',
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            isExtraMapFeatEnabled: '',
            showPopupAddServiceAppointment: false,
            UserList: null,
            IsWorkOrderView: true,
            WorkOrdrId: '',
            showquickviewModal: false,
            IsWorkOrderDetails: null,
            mapObj: [],
            latLng: null,
            shownotelist: false,
            IsFSMVIEWMODE: '',
            isViewMode: null,
            statuslist: false,
            IsExtraMapFetureEnabled: '',
            SchduleAppointment: {},
            ObjectToAddServiceAppointment: {},
            appliedFilter: [],
            searchCondition: '',
            latLong:'',
            ViewListId:'',
            CompanyUserType:''      
        }
    },
    created: async function () {
        var vm = this;
        vm.viewType = ((typeof localStorage.viewType == 'undefined' || localStorage.getItem('viewType') == 'true') ? 'Card View' : 'List View');
        if (vm.viewType == 'Card View') {
            vm.WorkOrderListing = false;
            vm.WorkOrderCard = true;
        } else {
            vm.WorkOrderListing = true;
            vm.WorkOrderCard = false;
        }
        await vm.GetDdlRecords_ViewList();
        await vm.fetchRecords();
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "USERS");
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Work_Order_Client_list");
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Work_Order_Parent_Statuscode");
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Work_Order_Child_Statuscode");
        await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Work_Type_List");
        vm.CheckConfigurationValueByKey('', 'FSMVIEWMODE')
        vm.isViewMode = this.IsFSMVIEWMODE == "yes" ? 1 : 0;
        await vm.IsExtraMapFeature();
        vm.CompanyUserType = vm.GetUserInfo.UserType;
       
    },
    methods: {
        openFilter() {
            this.showFilter = true;
        },
        widgetClick: function (code) {
            var vueObj = this;
            vueObj.selectedWidgetCode = code.name;
            if (vueObj.selectedWidgetCode == 'In progress') {
                vueObj.selectedWidgetCode = 'inprogress';
            }
            if (vueObj.selectedWidgetCode == 'Completed') {
                vueObj.selectedWidgetCode = 'completed';
            }
            if (vueObj.selectedWidgetCode == 'Need To Reschedule') {
                vueObj.selectedWidgetCode = 'needtoreschedule';
            }
            if (vueObj.selectedWidgetCode == 'Scheduled') {
                vueObj.selectedWidgetCode = 'scheduled';
            }
            if (vueObj.selectedWidgetCode == 'UnScheduled') {
                vueObj.selectedWidgetCode = 'unscheduled';
            }
            vueObj.fetchRecords();
        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.fetchRecords();
        },
        CheckConfigurationValueByKey(modulename, checkKey) {
            var url = `moduleName=${modulename}&configkey=${checkKey}`;
            DataServices.CheckConfigurationValueByKey(url).then(response => {
                if (response.data != null) {
                    this.IsFSMVIEWMODE = response.data.CONFIG_DATA_VALUE;
                }
            });
        },
        IsExtraMapFeature: async function () {
            await DataServices.IsExtraMapFeature().then(response => {
                if (response.data == 'locating') {
                    this.IsExtraMapFetureEnabled = true;
                }
                else {
                    this.IsExtraMapFetureEnabled = false;
                }
            });
        },
        ShowHideMap: function () {
            if (this.isShowMap == false) {
                this.isShowMap = true;
                this.mapHeaderText = 'Collapse Map'
            }
            else {
                this.isShowMap = false;
                this.mapHeaderText = 'Expand Map'
            }
        },
        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.fetchRecords();
        },
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {                               
                case "openFilterPopUp":
                    this.openFilter();
                    break;                
                case "openaddWorkOrder":
                    this.openPopupForAddWorkOrder(null);
                    break;
                case "removeFilter":
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.fetchRecords();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
            }
        },
        openFilter() {
            this.showFilter = true;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'WorkOrderDetails':
                    this.WorkOrderDetails(item);
                    break;
                case 'openQuickViewpopup':
                    this.openQuickViewpopup(item);
                    break;
                case 'ManageNote':
                    this.openNoteListPopUp(item);
                    break;
                case 'WorkOrderStatus':
                    this.WorkOrderStatus(item)
                    break;
                case 'ViewonMap':
                    this.ViewonMap(item.ticket_id, item);
                    break;
                case 'SchduleAppointment':
                    this.openScheduledAppointmentPopup(item);
                    break;
                case 'AddServiceAppointment':                    
                    this.openAddServiceAppointment(item);
                    break;
            }
        },
        ViewonMap: function (ticketId, itm) {            
            var vm = this;
            vm.latLng = itm;
            vm.isShowMap = true;
        },
        openNoteListPopUp(itm) {            
            this.shownotelist = true;
            this.IsWorkOrderDetails = itm;
        },
        WorkOrderStatus(itm) {
            this.statuslist = true;
            this.IsWorkOrderDetails = itm;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.fetchRecords();
        },
        openPopupForAddWorkOrder: function (id) {
            if (id == undefined){
                id = 0;
            }
            this.ticketSourceId = id;
            this.AddWorkOrderModal = !this.AddWorkOrderModal;
        },
        getSaStatusData(sa_status) {
            var SA_Status = '';
            if (sa_status != '' && sa_status != 'Completed') {
                SA_Status = ' - ' + sa_status
            }
            return SA_Status;
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.fetchRecords();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        openQuickViewpopup(item) {
            this.IsWorkOrderDetails = item;
            this.showquickviewModal = true;
        },        
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Quick View':
                        action.isVisible = true;
                        break;
                    case 'Details':
                        action.isVisible = true;
                        break;
                    case 'Notes':
                        action.isVisible = true;
                        break;
                    case 'View on Map':
                        action.isVisible = true;
                        break;
                    case 'Schdule':
                        action.isVisible = row.status_id != 'Completed' && row.status_id != 'Failed' && row.status_id != 'Canceled' && row.SA_Id != null;
                        break;
                    case 'Status':
                        action.isVisible = this.isViewMode == 0 ? true : false;
                        break;
                    case 'Add Service Appointment':
                        action.isVisible = row.status_id != 'Completed' && row.status_id != 'Failed' && row.status_id != 'Canceled' && row.SA_Id != null;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }, 
        fetchRecords: async function () {           
            var vueObj = this;
            vueObj.isLoading = true;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }
            else {
                if (vueObj.appliedFilter.length == 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                }
            }
            var url = `pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&search=${vueObj.searchCondition}&code=${vueObj.selectedWidgetCode}`;
            await DataServices.WorkOrderList(url).then(function (response) {                
                vueObj.isLoading = false;
                vueObj.latLong=response.data.result.mapdetail[0].LATITUDE+','+response.data.result.mapdetail[0].LONGITUDE;
                if (response.data != "" && response.data != null) {
                    vueObj.Headers = response.data.result.schema;
                    vueObj.Headers.forEach((column) => {
                        if (column.COLUMN_NAME == 'workorder_number') {
                            column.DISPLAY_NAME = 'WO Number'
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'sa_status') {
                            column.DISPLAY_NAME = 'SA Status'
                            column.settings = {
                                isInSlot: true
                            }
                        }

                    })
                    if (response.data.result.data.length > 0 && response.data.result != "") {
                        vueObj.WorkOrderData = response.data.result.data;
                        vueObj.AllResponseData = response.data.result;
                        vueObj.CurrentItem = null

                        vueObj.IsSupervisor = response.data.result.IS_SUPERVISOR;
                        vueObj.IsLocator = response.data.result.IS_LOCATOR;
                        vueObj.UserType = response.data.result.USERTYPE_NAME;
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } 
                    else {
                        vueObj.noRecord = true;
                        vueObj.WorkOrderData = [];
                        vueObj.TotalRecords = 0
                    }
                    if (response.data.result.CompletedSACount == 0) {
                        vueObj.CompletedSACount = 0;
                    }
                    else {
                        vueObj.widgets[0].value = response.data.result.CompletedSACount;
                    }
                    if (response.data.result.InProgressSACount == 0) {
                        vueObj.InProgressSACount = 0;
                    }
                    else {
                        vueObj.widgets[1].value = response.data.result.InProgressSACount;
                    }
                    if (response.data.result.NeedToRescheduleSACount == 0) {
                        vueObj.NeedToRescheduleSACount = 0;
                    }
                    else {
                        vueObj.widgets[2].value = response.data.result.NeedToRescheduleSACount;
                    }
                    if (response.data.result.ScheduledSACount == 0) {
                        vueObj.ScheduledSACount = 0;
                    }
                    else {
                        vueObj.widgets[3].value = response.data.result.ScheduledSACount;
                    }
                    if (response.data.result.UnscheduledSACount == 0) {
                        vueObj.UnscheduledSACount = 0;
                    }
                    else {
                        vueObj.widgets[4].value = response.data.result.UnscheduledSACount;
                    }
                    if (response.data.result.CancelledSACount == 0) {
                        vueObj.CancelledSACount = 0;
                    }
                    else {
                        vueObj.widgets[5].value = response.data.result.CancelledSACount;
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.WorkOrderData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                response.data.result.data.forEach(function (row) {
                    var date=vueObj.$options.filters.formatDate(row.created_at);
                    var obj = {
                        latlong: vueObj.latLong,
                        refValue: row.ticket_number,
                        popupHeaderHtml: '<div id="siteNotice"></div><h1 id="firstHeading" class="firstHeading">' + row.ticket_number + '<span class="status">' + row.priority + '</span></h1>',
                        popupHeaderColor: "",
                        popupBodyHtml: '<p class="details"><i class="fa fa-calendar" style="font-size: 19px;margin-right: 6px;"></i><span style="text-transform:uppercase !important;">' + date + '</span></p>' +
                            '<p class="details"><i class="fa fa-wrench" style="font-size: 19px;margin-right: 6px;"></i><span>' + (row.work_type_id) + '</span></p>' +
                            '<p class="details"><i class="fa fa-map-marker" style="font-size: 25px;margin-right: 7px;margin-left: 3px;"></i><span>' + row.Address + '</span></p>',
                        popupFooterHtml: '<span class="p-2"><a href="javascript:;"><i class="fa fa-file-pdf-o"></i> 0</a><a href="javascript:;"><i class="fa fa-image"></i> 0</a><a href="javascript:;"><i class="fa fa-video-camera"></i> 0</a></span>',
                        pinLatLong: {
                            latitude: row.location_nw,
                            longitude: row.location_nw
                        },
                        polygons: [
                            {
                                name: "",
                                latLong: [{
                                    latitude: row.location_nw,
                                    longitude: row.location_nw,
                                }
                                ]
                            }
                        ],
                        address: row.Address,
                        priority: row.priority,
                        color_code: row.color_code
                    }
                    vueObj.mapObj.push(obj);
                })
                
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        ShowTicketOnMap: function (item, index) {
            if (!this.IsObject(item)) {
                item = JSON.parse(item);
            }
            var vm = this;
            vm.ShowSupervisorMap = false;
            if (vm.ExpandMap == 'true' || vm.IsMapView == false) {
                $(window).scrollTop(1000);
            }
            $("html, body").animate({ scrollTop: 0 }, "slow");
            vm.CurrentItem = item;
            vm.Latitude = item.location_nw
            vm.Longitude = item.location_se
            vm.MapIndex = index;
            if ($("#divmapdata").css('display') == 'none') {
                var icon = $('#icnToggleMap');
                var text = $('#spnToggleMap');
                $("#divmapdata").slideToggle();
                if (icon.hasClass('fa-angle-double-down')) {
                    icon.removeClass('fa-angle-double-down');
                    icon.addClass('fa-angle-double-up');
                    text.html('Collapse map');
                }
                setTimeout(function () {
                    if ($("#divmapdata").css("display") == "block") {
                        LoadMapJsCode(checkVue.AllResponseData, checkVue);
                    }
                    setTimeout(function () { ShowTicketOnMapIndexMap(item, index); }, 700);
                }, 500);
            } else {
                ShowTicketOnMapIndexMap(item, index);
            }
        },
        ShowEtaMap: function (type) {
            var vm = this;            
            var url = "/Ticketing/LocatorHeatMap?type=locator";
            if (type == "super") {
                url = "/Ticketing/SuperHeatMap?type=supervisor";
            }
            if (type == "super") {

                vm.ShowLocatorMap = false;
                if (vm.ShowSupervisorMap) {
                    vm.ShowSupervisorMap = false;
                    RemoveAllFeatureAndImages();
                }
                else {
                    vm.ShowSupervisorMap = true;
                    LoadLocatoronMap();
                }
            }
            else {
                vm.ShowSupervisorMap = true;
                if (vm.ShowLocatorMap) {
                    vm.ShowLocatorMap = false;
                    $(".anchideduration,.ancshowduration,.bg-gray-faint").hide();
                    RemoveAllFeatureAndImages();
                }
                else {
                    vm.ShowLocatorMap = true;
                    vm.ShowSupervisorMap = false;
                    LoadETAMap();
                }
            }
            $("#divmapdata").slideDown();
        },
        changeWorkOrderView: function () {
            var vm = this;
            vm.WorkOrderListing = !vm.WorkOrderListing;
            vm.WorkOrderCard = !vm.WorkOrderCard;
            if (vm.viewType == 'List View') {
                vm.viewType = 'Card View';
                vm.classView = 'fa fa-th-large'
            }
            else {
                vm.viewType = 'List View';
                vm.classView = 'fa fa-th-list';

            }
            localStorage.setItem("viewType", vm.viewType);
            vm.fetchRecords();
        }, 
        OnApplyView(viewId) {  
                      
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Ticketing',
                    SubmoduleCode: 'FSM_WORKORDER'
                };
                var objectPostString = JSON.stringify(obj);                     
                DataServices.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.fetchRecords();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        DefaultView: function () {                                   
            var vm = this;
            var obj = {
                ModuleName: 'Ticketing',
                SubmoduleCode: 'FSM_WORKORDER'
            };            
            var objectPostString = JSON.stringify(obj);
            DataServices.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        //vm.$emit('RefreshManageView');
                        vm.fetchRecords();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        GetDdlRecords_ViewList: async function () {
          //  debugger
            var vm = this;
            var url = `moduleName=Ticketing&subModuleCode=FSM_WORKORDER&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataServices.GetViewList(url).then(function (response) {
                vm.ViewList = response.data.DATA;
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                   
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if( vm.ViewListId > 0){
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;    
                    }else{
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);  
            });           
        },   
        openScheduledAppointmentPopup: function (itm) {
            var vm = this;
            vm.SchduleAppointment = itm;
            vm.IsSchduleAppointment = !vm.IsSchduleAppointment;
        },
        openAddServiceAppointment: function (item) { 
            this.ObjectToAddServiceAppointment = item
            this.showPopupAddServiceAppointment = !this.showPopupAddServiceAppointment;
        },
        WorkOrderDetails: function (item) {            
            this.$router.push({ name: "WorkOrderDetail", params: { id: item.id,ticket_id:item.ticket_id }});
        }
    }
}
