async function bindleftCommonSearchdropdown(instance, moduleName, filtername, data, AllCount) {
    var vm = instance;
    var param = "moduleName='" + moduleName + "'&type=" + filtername + ""
    var param1 = "moduleName=FSM&type=" + filtername + ""
    if (filtername == 'USERS') {
        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[2].listOptions.push(obj);
                vm.leftSearchFields[4].listOptions.push(obj);
            });
        });
    }

    else if (filtername == 'Work_Order_Client_list') {

        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[7].listOptions.push(obj);
            });
        });
    }
    else if (filtername == 'Work_Order_Parent_Statuscode') {

        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[5].listOptions.push(obj);
            });
        });
    }
    else if (filtername == 'Work_Order_Child_Statuscode') {

        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[6].listOptions.push(obj);
            });
        });

    }
    else if (filtername == 'Work_Type_List') {
        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[8].listOptions.push(obj);
            });
        });
    }


};

export default { buttons, leftsearchSchema, listActions, bindleftCommonSearchdropdown };