        vm.sourceId = vm.WorkOrderDetails.ticket_id;
        await vm.openWorkOrderQuickView();
    },
    methods: {
        sortdata() { },
        actionButtonClick() { },
        commonHeaderButtonClick() { },
        pagerMethod() { },
        leftCommonSearch() { },
        renderActions() { },
        openAttachmentModel: function () {
            var vueObj = this;
            vueObj.showAttachmentModal = true;
            $("#divAttachment").scrollTop(0);
        },
        openSpecificImage(attachmentPath, attachmentFileName) {            
            var vm = this;
            vm.galleryImages=[];
            var obj = {
                path: attachmentPath,                           
                caption: attachmentFileName.split('.')[0]
            }
            vm.galleryImages.push(obj);  
            vm.visible=true;
        },
        FetchAttachmentData: function () {
            var vueObj = this;
            vueObj.IsAddAttachment = true;
            vueObj.galleryImages = [];
            vueObj.AttachmentList = [];
            vueObj.galleryFilesView = [];
            vueObj.isLoading = true;
            var url = `TicketId=${vueObj.WorkOrderDetails.ticket_id}&WorkOrderId=${vueObj.WorkOrderDetails.id}`;
            DataServices.AttachmentList(url).then(function (response) {
                vueObj.isLoading = false;
                if (response.data.status == "Success") {   
                    debugger                 
                    vueObj.AttachmentList = response.data.result.DATA;
                    vueObj.ConfigData = response.data.result.CONFIGDATA;
                    if (vueObj.AttachmentList != null && vueObj.AttachmentList.length > 0) {
                        vueObj.galleryFilesView = $.grep(vueObj.AttachmentList, function (element, index) {
                            vueObj.noRecord = false;
                            return (JSON.parse(element.ATTACHMENT_DESC).Extension != 'pdf' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'word' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'excel' && JSON.parse(element.ATTACHMENT_DESC).Extension != 'txt');
                        });
                        $.each(vueObj.galleryFilesView, $.proxy(function (index1, item) {
                            vueObj.galleryImages.push(item.ATTACHMENT_PATH);
                        }, this));
                    }
                    else {
                        vueObj.noRecord = true;
                    }
                }
            });
        },
        openServiceAppointmentQuickView() {
            var vueObj = this;
            vueObj.isLoading = true;
            var apiUrl = `id=${vueObj.WorkOrderDetails.id}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&search=`
            DataServices.GetServiceAppointmentByWorkOrderId(apiUrl).then(response => {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.ServiceAppointmentList = response.data.result.DATA;
                        vueObj.isLoading = false;
                    }
                }
            });
        },
        openWorkOrderQuickView: async function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var apiUrl = `str_ticket_id=${vueObj.WorkOrderDetails.id}&moduleName=FSM&subModuleCode=FSM_WORKORDER`
            await DataServices.ViewPageDetails(apiUrl).then(function (response) {
                vueObj.isLoading = false;
                if (response.data.result != null) {
                    var fullLeftJSON = response.data.result.LEFTDATA;
                    fullLeftJSON = $.grep(fullLeftJSON, function (item, index) {
                        if (item.name == 'LOCATION_SE') {
                            vueObj.LOCATION_SE = item.value;
                        }
                        if (item.name == 'LOCATION_NW') {
                            vueObj.LOCATION_NW = item.value;
                        }
                        if (item.name == 'Address') {
                            vueObj.FORMATED_ADDRESS = item.value;
                        }
                        if (item.name == 'ticket_number') {
                            vueObj.TICKETNUMBER = item.value;
                        }
                        if (item.form_field_visibility == 'NO') {
                            return;
                        }
                        if (item.dt_code == 'select' && item.picklist_options == "Lookup") {
                            item.value = vueObj.FilterDDLandNormalValue(item.value);
                        }
                        return item.form_field_visibility == 'YES';
                    });
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            groupId = item.group_id;
                            var chkGroup = $.grep(vueObj.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vueObj.LeftViewData.push(objGroup);
                                }
                            }
                        }, this));
                    }
                    vueObj.mapObj = [];
                    var obj = {
                        refValue: vueObj.TICKETNUMBER,
                        popupHeaderHtml: '',
                        popupHeaderColor: "",
                        popupBodyHtml: '',
                        popupFooterHtml: '',
                        pinLatLong: {
                            latitude: vueObj.LOCATION_SE,
                            longitude: vueObj.LOCATION_NW
                        },
                        polygons: [
                            {
                                name: "",
                                latLong: [{
                                    latitude: vueObj.LOCATION_SE,
                                    longitude: vueObj.LOCATION_NW,
                                }
                                ]
                            }
                        ],
                        address: vueObj.FORMATED_ADDRESS,
                        priority: '',
                        color_code: ''
                    }
                    vueObj.mapObj.push(obj);
                    if (response.data.result.FSM_MAP_CONFIG[0].TYPE == "Google") {
                        var script = document.createElement("script");
                        script.type = "text/javascript";
                        if (script.readyState) {
                            script.onreadystatechange = function () {
                                if (script.readyState == "loaded" || script.readyState == "complete") {
                                    script.onreadystatechange = null;
                                    //LoadMapBox("MapBox", response.data.result.MAP_CRED[0].API_KEY, response.data.result);
                                }
                            }
                        } else {
                            script.onload = function () {
                                initializeGoolgeMap("Google", response.data.result.FSM_MAP_CONFIG[0].API_KEY, obj);
                            }
                        }
                        script.src = "https://maps.googleapis.com/maps/api/js?key=" + response.data.result.FSM_MAP_CONFIG[0].API_KEY + "&libraries=places";
                        document.getElementsByTagName("head")[0].appendChild(script)
                    }
                    else if (response.data.result.FSM_MAP_CONFIG[0].TYPE == "MapBox") {
                        var script = document.createElement("script");
                        script.type = "text/javascript";
                        if (script.readyState) {
                            script.onreadystatechange = function () {
                                if (script.readyState == "loaded" || script.readyState == "complete") {
                                    script.onreadystatechange = null;
                                    //LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                                }
                            }
                        } else {
                            script.onload = function () {
                                LoadMapBox("MapBox", response.data.result.FSM_MAP_CONFIG[0].API_KEY, obj);
                            }
                        }
                        script.src = "//api.mapbox.com/mapbox-gl-js/v1.8.0/mapbox-gl.js";
                        document.getElementsByTagName("head")[0].appendChild(script)
                        var script1 = document.createElement('script');
                        script1.type = 'text/javascript';
                        script1.src = "//api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-geocoder/v4.4.2/mapbox-gl-geocoder.min.js";
                        document.getElementsByTagName('head')[0].appendChild(script1);
                    }
                    else {
                        var script = document.createElement("script");
                        script.type = "text/javascript";
                        if (script.readyState) {
                            script.onreadystatechange = function () {
                                if (script.readyState == "loaded" || script.readyState == "complete") {
                                    script.onreadystatechange = null;
                                    //LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                                }
                            }
                        } else {
                            script.onload = function () {
                                LoadLeafLetQuickview("LeafLet", response.data.result.FSM_MAP_CONFIG[0].API_KEY, obj);
                            }
                        }
                        script.src = "//unpkg.com/leaflet@1.6.0/dist/leaflet.js";
                        document.getElementsByTagName("head")[0].appendChild(script)
                        var script1 = document.createElement('script');
                        script1.type = 'text/javascript';
                        script1.src = "/Scripts/FsmMaps/dist/leaflet_markerclusterer.js";
                        document.getElementsByTagName('head')[0].appendChild(script1);
                    }
                    setTimeout(() => {
                        vueObj.SetResponsiveTab();
                        vueObj.CheckBoxBootstrap();
                        vueObj.RadioButtonBootstrap();
                    }, 500);
                }
            });
        },
        FetchTrackHistoryRecords() {
            var vueObj = this;
            vueObj.isLoading = true;
            var apiUrl = `ticketingId=${vueObj.WorkOrderDetails.ticket_id}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&workCodeId=${vueObj.WorkOrderDetails.id}&lang=EN`
            DataServices.TrackHistoryList(apiUrl).then(response => {
                vueObj.isLoading = false;
                vueObj.TimeTrackHistoryList = [];
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.TimeTrackHistoryList = response.data.result.DATA;
                        if (vueObj.TimeTrackHistoryList != null && vueObj.TimeTrackHistoryList.length > 0) {
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                        }
                    }
                }
            });
        },
        LoadNotesForQuickView: async function (moduleName) {
            var vm = this;
            vm.NoteList = [];
            vm.isLoading = true;
            if (moduleName == null || moduleName == 'undefined') {
                moduleName = vm.moduleName;
            }
            var url = `ticketId=${vm.sourceId}&isSuptTckt=false&moduleName=${moduleName}&isView=1`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                    $("#tab-notes-content-quickview").removeClass("d-none");
                }
                else {
                    $("#div_NotesLabel").removeClass("d-none");
                    vm.noRecord = true;
                }
            });
        },
        LoadTicketTabData: function (ctrlClass, ticketId, isFor) {
            var url = "";
            var vueObj = this;
            if (ctrlClass != "") {
                if (ctrlClass == "addnotes") {
                    url = '/Ticketing/EditNote';
                }
                var aData = [];
                aData.push({ name: "ticketId", value: ticketId });
                if (ctrlClass == "addnotes" || ctrlClass == "transfer") {
                    aData.push({ name: "moduleName", value: isFor });
                }
                var objectPostString = JSON.stringify(aData);
                DataServices.EditNote(objectPostString).then((response) => {
                    if (response.TicketStatusId == 1003) {
                        $('#divloadticketdata').empty().html('');
                        ShowAlert(Globalize.localize('SorryThetickethavebeendeleted'), "failure", true, Globalize.localize('Alert'), true);
                    }
                    else {
                        if (isFor == "FSM") {
                            $('#hdnissupttckt').val('@ViewBag.IsSupportTicket');
                            $('#divloadticketdata').removeClass("hide").addClass("show");
                            $("#div_NotesLabel").addClass("d-none");
                            $("#tab-notes-content-quickview").addClass("d-none");
                            vueObj.isShowAddNote = true;
                            $("#divNotes").scrollTop(0);
                        }
                        $('#divloadticketdata').show().empty().html(response.data);
                        $("#divNotes").scrollTop(0);
                    }
                });

            }
        },
        FetchReportIncidentRecords() {
            var vueObj = this;
            var apiUrl = `ticketingId=${vueObj.WorkOrderDetails.ticket_id}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&workCodeId=${vueObj.WorkOrderDetails.id}&lang=EN`;
            DataServices.FetchReportIncidentRecords(apiUrl).then(response => {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.IncidentList = response.data.result.data;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        if (vueObj.IncidentList.length > 0) {
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.NoRecord = false;
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                    else {
                        vueObj.NoRecord = true;
                    }
                }
            });
        },
        FetchViewStatusHistoryRecords() {
            var vueObj = this;
            var apiUrl = `ticketingId=${vueObj.WorkOrderDetails.ticket_id}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&workOrderId=${vueObj.WorkOrderDetails.id}`;
            DataServices.FetchViewStatusHistoryRecords(apiUrl).then(response => {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.WorkOrderStatusHistoryList = response.data.result.DATA;
                        if (vueObj.WorkOrderStatusHistoryList.length > 0) {
                            $.each(vueObj.WorkOrderStatusHistoryList, function (index, item) {
                                if (item.FORMDATA != null) {
                                    item.selectedFormDataId = item.FORMDATA[0].Formdata_Id;
                                }
                            });
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                        }
                    } else {
                        vueObj.noRecord = true;
                    }
                }
                else {
                    vueObj.noRecord = true;
                }
            })
        },
        ApproveRejectStatus: function (item, type) {
            var vueObj = this;
            var msg = "";
            var alertTitle = "";
            if (type == "approve") {
                msg = 'Are you sure you want to Approve the Status?';
                alertTitle = 'approve';
            }
            else {
                msg = 'Are you sure you want to Reject the Status?';
                alertTitle = 'Reject';
            }
            vueObj.confirmR(msg, true, alertTitle, function () {
                var obj = {
                    Id: item.ID,
                    IsApprove: type == "approve" ? 1 : 0,
                    SupervisorComment: item.SupervisorDescription != undefined ? item.SupervisorDescription : null
                };
                var objectPostString = JSON.stringify(obj);
                DataServices.ApproveRejectStatus(objectPostString).then(function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            if (response.data.result.MSG == "SUCCESS") {
                                if (response.data.result.SAVED_FORM_DATA_ID > 0 && type == "approve") {
                                    vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("Status")]), "success", true, vueObj.$t("Alert"));
                                    vueObj.FetchViewStatusHistoryRecords();
                                }
                                else {
                                    vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("Status")]), "success", true, vueObj.$t("Alert"));
                                    vueObj.FetchViewStatusHistoryRecords();
                                }
                            }
                            else {
                                vueObj.ShowAlert(vueObj.$t("ErrorWhileUpdating", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                            }
                        } else {
                            vueObj.ShowAlert(vueObj.$t("ErrorWhileUpdating", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                        }
                    } else {
                        vueObj.ShowAlert(vueObj.$t("ErrorWhileUpdating", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                    }
                });
            });
        },
    }
}
</script>
